<template>
  <div class="relative block">
    <button @click="isOpen = !isOpen" class="focus:outline-none">
      <img src="https://cdn.pixabay.com/photo/2021/11/16/08/01/animal-6800387__340.jpg" class="ml-3 w-10 h-10 bg-gray-800 rounded-full" alt="avatar">
    </button>

    <button 
    @click="isOpen = false"
    v-if="isOpen"
    class="fixed top-0 right-0 bottom-0 w-full h-full bg-black opacity-50"
    ></button>
  </div>

  <div v-if="isOpen" class="flex absolute flex-col bg-white rounded-lg text-gray-500 py-2 w-48 right-8 top-20 shadow-md z-50">
    <a class="px-4 py-2 border-b hover:bg-indigo-500 hover:text-white" href="#">Account Setting</a>
    <a class="px-4 py-2 border-b hover:bg-indigo-500 hover:text-white" href="#">Support</a>
    <a class="px-4 py-2 hover:bg-indigo-500 hover:text-white" href="#">Logout</a>
  </div>
</template>

<script>
export default {
  created() {
    const handleEscape = (e) => {
      if(e.key == "Esc" || e.key == "Escape"){
        this.isOpen = false
      }
    }

    document.addEventListener("keydown", handleEscape)
  },
  data() {
    return {
      isOpen: false
    }
  }
}
</script>

