<template>
    <div>
      <router-link :to="{ name: 'Movie', params: { id: movie.id } }">
        <img :src="posterPath" alt="">
      </router-link>
      <h3>{{movie.title}}</h3>
      <div class="flex">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 text-yellow-500" viewBox="0 0 20 20" fill="currentColor">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
        {{movie.vote_average}} | {{ movie.release_date}}
      </div>
      <span class="text-sm text-gray-500">
        <span v-for="(gen, index) in movie.genre_ids" :key="gen" :gen="getgenre(gen, index)">
          {{ getgenre(gen, index) }}
        </span>
      </span>
    </div>
</template>

<script>
export default {
  props: {
    movie: {
      required: true
    },
    genre: {
      required: true
    }
  },
  computed: {
    posterPath() {
      if(this.movie.poster_path){
        return `https://image.tmdb.org/t/p/w1280/${this.movie.poster_path}`
      } else {
        return `https://via.placeholder.com/300x450`
      }
    }
  },
  methods: {
    getgenre(genId, index){
      if(this.genre[genId]){
        if(this.movie.genre_ids.length -1 == index){
          return this.genre[genId].name
        } else {
          return this.genre[genId].name + ','
        }
      }
      
    }
  }
}
</script>
