<template>
<div class="hidden md:block">
    <div class="flex">
    <div class="relative flex">
      <input
      ref="focy"
      type="text"
      class="px-5 rounded-full bg-gray-600 py-1 focus:outline-none"
      placeholder="search...(press '/' to focus)"
      @input="fetchSearch"
      v-model="checking"
      />
      <svg xmlns="http://www.w3.org/2000/svg" class="absolute top-2 right-2 h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
      </svg>
    </div>
    <div v-if="final" class="z-50 absolute top-20 bg-gray-700 rounded w-64">
      <ul v-if="searchresult.length != 0" class="h-72 overflow-auto">
        <li v-for="(app, index) in searchresult" :key="index" class="flex pt-4 pl-2 pb-2 pr-2 border-b border-gray-500">
            <router-link class="flex items-start p-1" :to="{ name: 'Movie', params: {id : app.id}}"  @click="reloadPage">
              <img class="w-16 mr-3" :src="searchPath(app.poster_path)" alt="">
              <div class="cursor-pointer">{{ app.title }}</div>
            </router-link>
        </li>
      </ul>
      <ul v-if="noresult" class="p-4">
        no result found for "{{ checking }}"
      </ul>
    </div>
    <Accountdrop />
  </div>
</div>
<div class="block md:hidden">
  <svg @click="shownav = !shownav" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor">
    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16" />
  </svg>

  <div v-if="shownav" class="absolute z-50 left-0 top-20 w-full bg-gray-800 shadow-lg">
    <ul class="text-white flex flex-col text-center ml-6">
        <li class="py-3">
          <router-link :to="{ name : 'Home' }" @click="shownav = false">Movies</router-link>
        </li>
        <li class="py-3">
          <router-link to="/actors" @click="shownav = false">Actors</router-link>
        </li>
        <li class="py-3">
          <input
      ref="focy"
      type="text"
      class="px-5  rounded bg-gray-600 py-1 focus:outline-none"
      placeholder="search"
      @input="fetchSearch"
      v-model="checking"
      />
        </li>
      </ul>
      <div v-if="final" class="shadow-lg">
        <ul v-if="searchresult.length != 0" class="h-72 overflow-auto">
        <li v-for="(app, index) in searchresult" :key="index" class="flex pt-4 pl-2 pb-2 pr-2 border-b border-gray-500">
            <router-link class="flex items-start p-1" :to="{ name: 'Movie', params: {id : app.id}}"  @click="reloadPage">
              <img class="w-16 mr-3" :src="searchPath(app.poster_path)" alt="">
              <div class="cursor-pointer">{{ app.title }}</div>
            </router-link>
        </li>
      </ul>
      <ul v-if="noresult" class="p-4">
        no result found for "{{ checking }}"
      </ul>
      </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import Accountdrop from './Accountdrop.vue'
export default {
  components: { Accountdrop },
  data() {
    return {
      searchresult: [],
      noresult: false,
      checking: "",
      final: true,
      shownav: false
    }
  },
  mounted() {
    this.keyHandler()
  },
  methods: {
    fetchSearch(event) {
      this.checking == "" ? this.final = false : this.final = true
      clearTimeout(damn)
      const damn = setTimeout( () => {
        if(event.target.value.length > 2){
          this.getsearch(event.target.value)
        }
      }, 1000)
    },

    async getsearch(term) {
      const some = await axios.get(`https://api.themoviedb.org/3/search/movie?&api_key=04c35731a5ee918f014970082a0088b1&query=${term}`)
      this.searchresult = some.data.results
      this.noresult = !this.searchresult.length ? true : false
    },

    searchPath(poster_path) {
      if(poster_path){
        return `https://image.tmdb.org/t/p/w300/${poster_path}`
      } else {
        return `https://via.placeholder.com/300x450`
      }
    },

    keyHandler() {
      let widow = this

      window.addEventListener("click", (e) => {
        if(!this.$el.contains(e.target)){
          this.final = false
          this.checking = ""
        }
      })
      window.addEventListener("keypress", (e) => {
        if(e.key == "/"){
          e.preventDefault()
          widow.$refs.focy.focus()
        }
      })

      window.addEventListener("keydown", (e) => {
        if(e.key == "Escape"){
          this.final = false
          this.checking = ''
        }
      })
    },
    reloadPage() {
      this.final = false
      this.shownav = false
    }
    
  }
}
</script>