<template>
<transition name="fade">
  <div v-show="appear" class="p-2 bg-indigo-500 fixed bottom-3 rounded right-4 shadow-2xl cursor-pointer">
    <svg @click="toTop" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M5 15l7-7 7 7" />
    </svg>
  </div>
</transition>

</template>

<script>
export default {
  data() {
    return {
      appear: false
    }
  },
  mounted() {
    this.detectwindowscroll()
  },
  methods: {
    toTop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      })
    },
    detectwindowscroll() {
      window.addEventListener("scroll", () => {
        if (window.scrollY > 100) {
          this.appear = true
        } else {
          this.appear = false
        }
      })
    }
  }
}
</script>

<style>
  .fade-enter-from,
  .fade-leave-to {
    opacity: 0;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s;
  }
</style>