<template>
  
  <div v-if="movies.length">
    <h2 class="text-lg text-yellow-500 uppercase p-4">popular movies</h2>
    <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 mx-5">
      <Movielist v-for="movie in movies" :key="movie.id" :movie="movie" :genre="genre"/>
    </div>
  </div>
  <div v-else>
    <Skeleton />
    <Skeleton />
    <Skeleton />
  </div>

    
    

</template>

<script>
import Movielist from './hello/Movielist.vue'
import Skeleton from './Skeleton.vue'
import axios from 'axios'
export default {
  components: { Movielist, Skeleton },
  data() {
    return {
      movies: [],
      genre: []
    }
  },
  async mounted() {
    this.fetchGenre()
    const response = await axios.get("https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=04c35731a5ee918f014970082a0088b1&page=1")
    this.movies = response.data.results
  },

  methods: {
    async fetchGenre() {
      const gen = await axios.get('https://api.themoviedb.org/3/genre/movie/list?api_key=04c35731a5ee918f014970082a0088b1&language=en-US')
      this.genre = gen.data.genres
    }
  },
  
}

  
</script>
